import { Listbox, Transition } from '@headlessui/react';
import clsx from 'clsx';
import React, { Fragment, forwardRef } from 'react';
import { twMerge } from 'tailwind-merge';
import { getDistanceToBottom } from '../../utils/getDistanceToBottom';
import { FunctionalIcon } from '../icon/Icon';
import { DropDownProps } from './drop-down.interface';

const MENU_HEIGHT = 268;

const getOptionItemClasses = (hovered: boolean, isActive) => {
  return clsx(
    'zep-flex zep-w-full zep-items-center zep-justify-between zep-bg-background-light zep-px-1 zep-py-0.75',
    { 'zep-bg-background-medium': hovered },
    { 'zep-bg-background-medium': isActive },
    { 'zep-pr-3': isActive },
  );
};

export const DropDown: React.FC<DropDownProps> = forwardRef<HTMLElement, DropDownProps>(
  ({ disabled = false, items, message, label, placeholder, readOnly = false, state = 'default', ...props }, ref) => {
    const buttonRef = React.useRef<HTMLButtonElement>(null);
    const hasError = state === 'error';
    const disabledOrReadonly = disabled || readOnly;
    const iconColor = hasError ? 'zep-text-error' : state === 'info' ? 'zep-text-info' : '';
    const selectedItem = props?.value;

    return (
      <div
        className={twMerge(
          clsx(
            'zep-relative',
            'zep-inline-block',
            'zep-text-left',
            'zep-w-full',
            {
              'zep-cursor-not-allowed zep-opacity-disabled': disabled,
            },
            props.className,
          ),
        )}
        data-testid="zep-drop-down"
      >
        <Listbox disabled={disabledOrReadonly} {...props} ref={ref}>
          {({ open }) => {
            const distanceToBottom = getDistanceToBottom(buttonRef.current);
            const openOnTop = distanceToBottom < MENU_HEIGHT;
            const showPlaceholder = readOnly ? undefined : placeholder;
            const displayValue = selectedItem?.name || showPlaceholder;
            const color = disabled
              ? 'zep-text-opacity-disabled'
              : !selectedItem?.name && !readOnly
              ? 'zep-text-typography-dark-100 zep-text-opacity-70'
              : 'zep-text-typography-dark-100';

            // input
            const buttonClasses = clsx(
              'zep-inline-flex',
              'zep-justify-between',
              'zep-rounded-4',
              'zep-border-solid',
              'zep-pl-1',
              'zep-pr-0.75',
              'zep-py-0.75',
              'zep-font-roboto',
              'zep-w-full',
              'zep-outline-none',
              hasError ? 'zep-border-2' : 'zep-border-1',
              color,
              { 'zep-bg-background-medium': readOnly },
              { 'zep-ring': open },
              { 'zep-border-error': hasError },
              { 'zep-active:ring': !disabledOrReadonly },
              { 'zep-ring-offset-1': !disabledOrReadonly },
              { 'focus-visible:zep-ring': !disabledOrReadonly },
              { 'zep-ring-focus': !disabledOrReadonly },
              { 'zep-cursor-not-allowed': disabledOrReadonly },
            );

          const optionContainerClasses = clsx(
            'zep-absolute',
            'zep-drop-shadow-md',
            'zep-py-0.25',
            'zep-z-10',
            'zep-w-full',
            'zep-max-h-[268px]',
            'zep-origin-top-right',
            'zep-rounded-4',
            'zep-bg-background-light',
            'focus:zep-outline-none',
            'zep-overflow-hidden',
            'zep-overflow-y-auto',
            openOnTop ? 'zep-mb-[-25px]' : 'zep-mt-0.25',
            { 'zep-bottom-full': openOnTop },
          );

            const iconClasses = clsx(
              disabledOrReadonly ? 'zep-text-opacity-disabled' : 'zep-text-typography-dark-100',
              'zep-transition-transform',
              'zep-duration-500',
              'zep-shrink-0',
              {
                'zep-rotate-180': open,
              },
            );

            return (
              <>
                {label && (
                  <Listbox.Label
                    className={clsx('zep-block', 'zep-mb-0.5', {
                      'zep-text-opacity-disabled': disabled,
                    })}
                  >
                    {label}
                    {!props.required && <span className="zep-ml-0.25">(optional)</span>}
                  </Listbox.Label>
                )}

                <Listbox.Button
                  className={buttonClasses}
                  ref={buttonRef}
                  title={displayValue}
                  data-testid="zep-drop-down-button"
                >
                  <>
                    <span className="zep-truncate">{displayValue}</span>
                    <FunctionalIcon className={iconClasses} name="chevron-mini-down" role="img" size={24} />
                  </>
                </Listbox.Button>
                <Transition
                  as={Fragment}
                  enter="zep-transition zep-ease-out zep-duration-100"
                  enterFrom="zep-transform zep-opacity-0 zep-scale-95"
                  enterTo="zep-transform zep-opacity-100 zep-scale-100"
                  leave="zep-transition zep-ease-in zep-duration-25"
                  leaveFrom="zep-transform zep-opacity-100 zep-scale-100"
                  leaveTo="zep-transform zep-opacity-0 zep-scale-95"
                >
                  <Listbox.Options className={optionContainerClasses} data-testid="zep-drop-down-menu">
                    {items.map((item, i) => (
                      <Listbox.Option
                        key={item.id}
                        value={item}
                        title={item.name}
                        className={({ active, selected }) => getOptionItemClasses(active, selected)}
                      >
                        {({ selected }) => {
                          return (
                            <>
                              <span className="zep-truncate">{item.name}</span>
                              {selected && (
                                <FunctionalIcon
                                  name="check"
                                  role="img"
                                  size={24}
                                  className="zep-absolute zep-right-1"
                                />
                              )}
                            </>
                          );
                        }}
                      </Listbox.Option>
                    ))}
                  </Listbox.Options>
                </Transition>
              </>
            );
          }}
        </Listbox>
        {message && (
          <div
            className={clsx(
              'zep-mt-[6px]',
              { 'zep-text-typography-dark-100 zep-opacity-70': !hasError },
              'zep-text-0.875',
              'zep-flex',
              'zep-items-center',
              iconColor,
              { 'zep-text-error': hasError },
            )}
          >
            {state !== 'default' && (
              <FunctionalIcon
                name={hasError ? 'warning-triangle-filled' : 'info-circle-filled'}
                role="img"
                size={24}
                className="zep-mr-0.5 zep-shrink-0"
              />
            )}
            <span className='zep-mt-[2px]'>{message}</span>
          </div>
        )}
      </div>
    );
  },
);
